.row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -1rem;
  margin-left: -1rem;
}
.row.row-vertical-center {
  align-items: center;
}

.col {
  position: relative;
  padding-left: 1rem;
  padding-right: 1rem;
  flex: 0 0 100%;
  max-width: 100%;
  width: 100%;
}
@media (min-width: 768px) {
  .col {
    flex: 0 0 50%;
    max-width: 50%;
  }
}

* {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Ubuntu, "Helvetica Neue", Oxygen, Cantarell, sans-serif;
  font-weight: 400;
  line-height: 1.8;
}
*, *::before, *::after {
  box-sizing: border-box;
}

body {
  min-height: 100%;
  background-color: #ffffff;
  color: #333333;
}

.container {
  max-width: 960px;
  margin: 0 auto;
  width: 80%;
}

hr {
  border: 0;
  border-bottom: 1px #eaeaea solid;
  margin: 2rem 0;
}

code {
  font-family: monospace;
}

pre {
  background: #f3f4f4;
  color: #1abc9c;
  padding: 2rem;
  margin: 2rem 0;
  font-family: monospace;
  overflow-x: scroll;
}

img {
  max-width: 100%;
  height: auto;
}

h1, h2, h3, h4, h5, h6 {
  margin: 4rem 0 2rem 0;
  color: #333333;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Ubuntu, "Helvetica Neue", Oxygen, Cantarell, sans-serif;
  font-weight: bold;
}

h1 {
  font-size: 2.4rem;
  font-weight: 600;
}

h2 {
  font-size: 2.16rem;
}

h3 {
  font-size: 1.92rem;
}

h4 {
  font-size: 1.68rem;
}

h5 {
  font-size: 1.44rem;
}

h6 {
  font-size: 1.2rem;
}

.mute {
  color: #cccccc;
}

p {
  font-size: 1.2rem;
  margin: 0;
  margin-bottom: 1rem;
  line-height: 1.8;
}

ul, ol, dl {
  font-size: 1.2rem;
  margin: 1rem 0;
}

dl dt {
  font-weight: bold;
  padding-right: 2rem;
}
dl dd {
  margin-left: 2rem;
}

a {
  color: #1abc9c;
  text-decoration: underline;
  transition: all 0.2s;
}
a:hover {
  color: #0e6252;
}

code {
  font-family: monospace;
  background: #f3f4f4;
  color: #333333;
}

small {
  font-size: 0.84rem;
}

.card {
  background: #f3f4f4;
  padding: 2rem;
  margin: 2rem 0;
  border-radius: 2rem;
}

.btn {
  cursor: pointer;
  border: 1px #eaeaea solid;
  border-color: #1abc9c;
  padding: 1rem 2rem;
  border-radius: 2rem;
  text-decoration: none;
  transition: all 0.2s;
  background: #1abc9c;
  color: #ffffff;
}
.btn.btn-outline {
  color: #1abc9c;
  background: transparent;
}
.btn.btn-ghost {
  border: 0;
  background: white;
  color: #1abc9c;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.12);
}
.btn:hover {
  box-shadow: 0 7px 20px rgba(0, 0, 0, 0.12);
}

header, footer {
  margin: 2rem 0;
  padding: 2rem 0;
}

footer {
  border-top: 1px #eaeaea solid;
  margin-bottom: 0;
  color: #cccccc;
}

header {
  border-bottom: 1px #eaeaea solid;
  margin-top: 0;
}
@media (min-width: 768px) {
  header nav {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    flex-flow: row nowrap;
  }
  header nav > *:not(:last-child) {
    margin-right: 2rem;
  }
}
header nav .hamburger {
  float: right;
  position: relative;
  width: 2rem;
  height: 1rem;
  margin: 0;
  margin-top: 1.6rem;
  border: 0;
  padding: 0;
  background: transparent;
}
@media (min-width: 768px) {
  header nav .hamburger {
    display: none;
  }
}
header nav .hamburger span {
  position: absolute;
  height: 2px;
  border-radius: 2px;
  background: #1abc9c;
  transition: all 0.2s;
}
header nav .hamburger span:nth-child(1) {
  left: 0;
  top: 0;
  width: 50%;
}
header nav .hamburger span:nth-child(2) {
  left: 0;
  top: calc(50% - 1px);
  width: 100%;
}
header nav .hamburger span:nth-child(3) {
  right: 0;
  bottom: 0;
  width: 50%;
}
header nav .hamburger:hover, header nav .hamburger:focus, header nav .hamburger:active {
  outline: 0;
}
header nav .hamburger:hover span, header nav .hamburger:focus span, header nav .hamburger:active span {
  width: 100%;
}
header nav h5 {
  margin: 0;
  padding: 1rem 0;
}
header nav ul {
  display: none;
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  header nav ul {
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }
}
header nav ul > li {
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  header nav ul > li {
    margin-bottom: 0;
  }
}
header nav ul > li:not(:last-child) {
  margin-right: 2rem;
}
header.stiky {
  position: sticky;
  left: 0;
  top: 0;
  width: 100%;
  background: white;
  z-index: 999;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.12);
}

table {
  width: 100%;
  border-spacing: 0;
}
table thead > tr > th {
  padding: 1rem 2rem;
  background: #f3f4f4;
  font-weight: bold;
  text-align: left;
  border-bottom: 1px #eaeaea solid;
}
table tbody > tr > td {
  padding: 1rem 2rem;
  border-bottom: 1px #eaeaea solid;
}

fieldset {
  border: 0;
  padding: 0;
}

::placeholder {
  color: #f3f4f4;
  opacity: 1;
}

input[type=text],
input[type=email],
input[type=search],
textarea,
select {
  border: 1px #eaeaea solid;
  padding: 1rem 2rem;
  display: block;
  width: 100%;
  border-radius: 2rem;
  background: white;
}

select {
  appearance: none;
  position: relative;
}

label {
  padding-left: 2rem;
}

input[type=checkbox] + label,
input[type=radio] + label {
  padding: 0;
}